html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-size: 16px;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}

strong {
  font-weight: 500;
}

a {
  text-decoration: none;
}

button,
input,
textarea {
  outline: none;
}
